<template>
  <div id="notice" class="education_views">
    <div class="education_header" v-show="show1 == '1'">
      <ec-button class="header-buts" @click.native="show1 = '2'">新的通知</ec-button>
    </div>
    <div class="education_centont">
      <div v-if="show1 == '1'" class="notice_content">
        <EcTable :tableData="noticeData">
          <el-table-column prop="id" label="序号" width="60">
            <template slot-scope="scope">
              <span>{{scope.row.id}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="title" label="通知名称" width="150">
            <template slot-scope="scope">
              <span>{{scope.row.title}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="area" label="发布范围" width="150">
            <template slot-scope="scope">
              <span>{{scope.row.area}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="content" label="通知内容">
            <template slot-scope="scope">
              <span class="common_base">{{scope.row.content}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="type" label="发布时间" width="160">
            <template slot-scope="scope">
              <span>{{scope.row.publishTime}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="getTime" label="操作" width="80">
            <template slot-scope="scope">
              <span class="edit" @click="onEdit(scope.row)">编辑</span>
            </template>
          </el-table-column>
        </EcTable>
      </div>
      <div v-else>
        <SetNotice :data="data" @event1="onChild($event)"/>
      </div>
    </div>
    <EcPagination
      v-show="total>10"
      :total="total"
      :currentPage="current"
      @size-change="handleSizeChange($event)"
      @current-change="handleCurrentChange($event)"
    ></EcPagination>

    <!-- <div class="education_centont">
      <div v-if="show1 == '1'" class="notice_content">
        <EcTable :tableData="noticeData">
          <el-table-column prop="id" label="序号" width="60">
            <template slot-scope="scope">
              <span>{{scope.row.id}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="title" label="通知名称" width="150">
            <template slot-scope="scope">
              <span>{{scope.row.title}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="area" label="发布范围" width="150">
            <template slot-scope="scope">
              <span>{{scope.row.area}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="content" label="通知内容">
            <template slot-scope="scope">
              <span class="common_base">{{scope.row.content}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="type" label="发布时间" width="160">
            <template slot-scope="scope">
              <span>{{scope.row.publishTime}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="getTime" label="操作" width="80">
            <template slot-scope="scope">
              <span class="edit" @click="onEdit(scope.row)">编辑</span>
            </template>
          </el-table-column>
        </EcTable>
      </div>
      <div v-else>
        <SetNotice :data="data" @event1="onChild($event)"/>
      </div>
    </div>
    <EcPagination
      v-show="total>10"
      :total="total"
      :currentPage="current"
      @size-change="handleSizeChange($event)"
      @current-change="handleCurrentChange($event)"
    ></EcPagination> -->
  </div>
</template>
<script>
import EcButton from "@/components/EducationButton";
import EcTable from "@/components/EducationTable";
import EcPagination from "@/components/EducationPagination";
import SetNotice from "./notice-set";
import { queryNotice } from "@/api/marketing.js";

export default {
  components: {
    EcButton,
    EcTable,
    EcPagination,
    SetNotice
  },
  data() {
    return {
      show1: "1",
      noticeData: [],
      size: 10,
      current: 1,
      data: {},
      total: 0
    };
  },

  methods: {
    onChild(e) {
      this.show1 = e;
      this.save_queryNotice();
    },
    onEdit(data) {
      this.show1 = "2";
      this.data = data;
    },
    handleSizeChange(e) {
      this.size = e;
      this.save_queryNotice(this.size, this.current);
    },
    handleCurrentChange(e) {
      this.current = e;
      this.save_queryNotice(this.size, this.current);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "@/styles/mixin.scss";
@import "@/styles/variables.scss";
#notice {
  width: 100%;
  .notice_content {
    width: 90% !important;
    min-width: 880px;
    margin: 0 auto;
    border: 1px solid #e8eae9;
  }
  .edit {
    @include themify() {
      color: themed("title-active");
      cursor: pointer;
    }
  }
}
</style>


